import myFetch from '@/utils/myFetch'
import Qs from 'qs'
export function getTypes(){
	var param=Qs.stringify({});
	return  myFetch({
		method:'post',
		url:'/type/getTypes.do',
		data:param
	});
}
export function updateType(pParentId,pTypeId,pTypeName){
	var param=Qs.stringify({
		'parentId':pParentId,
		'typeId':pTypeId,
		'typeName':pTypeName
	});
	return myFetch({
		method:'post',
		url:'/type/updateType.do',
		data:param
	});
}
export function addType(pParentId,pTypeName,pTypeCode){
	var param=Qs.stringify({
		'parentId':pParentId,
		'typeName':pTypeName,
		'typeCode':pTypeCode
	});
	return myFetch({
		method:'post',
		url:'/type/addType.do',
		data:param
	});
}
export function deleteType(pTypeId){
	var param=Qs.stringify({
		'typeId':pTypeId
	});
	return myFetch({
		method:'post',
		url:'/type/deleteType.do',
		data:param
	});
}


 













