<template>
  <div>
    <el-button type="primary" @click='initTree'>初始化</el-button>
     
    <el-row>
      <el-col :span="16">
         <el-tree
            v-loading="loading"
            highlight-current
            :data="treeData"
            :props="defaultProps"
            @node-click="handleNodeClick"
            @node-expand="expandNode">
        </el-tree>
      </el-col>
      <el-col :span="8">
          <el-form  :model="treeForm" label-width="80px">
            <el-form-item label="名字">
              <el-input v-model="treeForm.Name"></el-input>
            </el-form-item>
            <el-form-item label="集合">
              <el-input v-model="treeForm.Type"></el-input>
            </el-form-item>
            <el-form-item label="编码">
              <el-input v-model="treeForm.Code"></el-input>
            </el-form-item>
          </el-form>
      </el-col>
    </el-row>
  </div>
</template>
<script>
    import {getConcepts} from '@/api/knowledge' 

    export default{
      data () {
        return {
          loading:false,
          count: 0,
          treeForm:{
            Name:null,
            Code:null,
            Type:null
          },
          treeData:[],
          defaultProps: 
            {
              children: 'children',
              label: 'label'
            }
          }
      },
      created () {
          
       },
      mounted(){
        
      },
      methods: {
        loadNode(node){
          var self=this
          self.loading=true
          console.log('获取子节点')
          getConcepts(node.conceptId)
          .then((response)=>{
            node.children=[]
            for(var j=0;j<response.data.length;j++){
              var childData = response.data[j]
              var child = {'label': childData.conceptName, 'children':[],'conceptId':childData.conceptId,'type':childData.conceptType,'code':childData.conceptCode}
              for (var i = 0; i<response.data[j].hasChildren; i++){
                child.children.push({})
              }
              node.children.push(child)
            }
            self.loading=false
          })
        },
        handleNodeClick(data){
           var self=this
            self.treeForm.Type=data.type
            self.treeForm.Name=data.label
            self.treeForm.Code=data.code
        },
        expandNode(data){
          var self=this
          self.loadNode(data)
          // for(var k=0;k<data.children.length;k++){
          //   self.loadNode(data.children[k])
          // }
        },
        initTree(){
          var self=this
          self.treeData=[]
          self.loading=true
          getConcepts("")
          .then((response)=>{
            var rootElement = {conceptId: response.data[0].conceptId, 'label': response.data[0].conceptName, 'children':[]}
            for ( var i = 0; i<response.data[0].hasChildren; i++){
              rootElement.children.push({})
            }
            self.treeData.push(rootElement)
            self.treeForm.Type=response.data[0].conceptType
            self.treeForm.Name=response.data[0].conceptName
            self.treeForm.Code=response.data[0].conceptCode
            self.loading=false
          })
        }
      }
    }
</script>


